'use strict';

// MODULES //

import MAX_SAFE_INTEGER from '@stdlib/constants-float64-max-safe-integer' ;
import { isPrimitive as isPositiveInteger } from '@stdlib/assert-is-positive-integer' ;
import getOwnPropertyNames from '@stdlib/utils-property-names' ;
import getPrototypeOf from '@stdlib/utils-get-prototype-of' ;
import isEnumerable from '@stdlib/assert-is-enumerable-property' ;
import hasOwnProp from '@stdlib/assert-has-own-property' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;


// MAIN //

/**
* Returns an array of an object's inherited enumerable property names.
*
* @param {*} value - input object
* @param {PositiveInteger} [level] - inheritance level
* @throws {TypeError} second argument must be a positive integer
* @returns {Array} a list of inherited enumerable property names
*
* @example
* var keys = inheritedKeys( {} );
*/
function inheritedKeys( value, level ) {
	var names;
	var cache;
	var obj;
	var tmp;
	var N;
	var n;
	var k;
	var i;

	if ( arguments.length > 1 ) {
		if ( !isPositiveInteger( level ) ) {
			throw new TypeError( format( '0k84I', level ) );
		}
		N = level;
	} else {
		N = MAX_SAFE_INTEGER;
	}
	if ( value === null || value === void 0 ) {
		return [];
	}
	// Get the value's prototype:
	obj = getPrototypeOf( value );

	// Walk the prototype chain collecting enumerable property names...
	names = [];
	cache = {};
	n = 1;
	while ( obj && n <= N ) {
		tmp = getOwnPropertyNames( obj );
		for ( i = 0; i < tmp.length; i++ ) {
			k = tmp[ i ];
			if (
				hasOwnProp( cache, k ) === false &&
				isEnumerable( obj, k )
			) {
				names.push( k );
			}
			cache[ k ] = true;
		}
		obj = getPrototypeOf( obj );
		n += 1;
	}

	return names;
}


// EXPORTS //

export default inheritedKeys;
